body {
  font-family: sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  height: 4em;
  align-items: center;
  font-size: 1.3em;
  padding: 0 2em;
}

.selectedLink {
  text-decoration: underline;
}

.navbar-right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 17em;
}

a {
  text-decoration: none;
}

.btnlink {
  border: none;
  background: none;
  cursor: pointer;
  height: 2em;
  font-size: inherit;
  color: red;
}

.book-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 15%;
  margin: 4px;
}

.book-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.book-detail {
  padding: 2px 16px;
}

.book-list-container {
  display: flex;
  flex-wrap: wrap;
  margin-left: 5em;
}

.book-card-img {
  width: 100%;
  height: 70%;
  cursor: pointer;
}

.book-container {
  display: flex;
  margin: 2em 10em;
  width: 70%;
}

.book-container .detail {
  margin: 0 3em;
}

.review-container,
.post-review-container {
  display: flex;
  align-content: space-between;
  flex-wrap: wrap;
  margin: 5em 10em;
  width: 70%;
}

.update-review-container {
  display: flex;
  align-content: space-between;
  flex-wrap: wrap;
}
.review-box {
  border: 1px solid;
  width: 100%;
  border-radius: 12px;
  padding: 1em;
  margin: 1em 0;
}

.review-text {
  line-height: 1.7em;
  font-size: 1.1em;
  padding: 0.7em 0;
}

.review-time {
  display: flex;
  flex-direction: row-reverse;
}

.post-review-textArea {
  font-family: sans-serif;
  line-height: 1.7em;
  font-size: 1.1em;
  padding: 0.4em;
  width: 100%;
  height: 10em;
}

.post-review-btn,
.update-review-btn,
.cancel-review-btn {
  background-color: #f9f9fa;
  border: 1px solid #0769c5;
  color: rgb(7, 6, 6);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  transition: background-color 0.4s;
}

.post-review-btn,
.update-review-btn:hover {
  background-color: #0769c5;
  color: white;
}

.review-delete-btn,
.review-edit-btn {
  height: 1.5em;
  cursor: pointer;
}
.review-header {
  display: flex;
  justify-content: space-between;
}

.modal {
  top: 33%;
  left: 33%;
  z-index: 99;
  position: fixed;
  align-items: center;
  background: #21212185;
}

.popup-heading {
  font-size: 1.2rem;
  margin: 0 auto;
}

.popup-box {
  margin: 0 auto;
  align-items: center;
  background: white;
  z-index: 100;
  padding: 3em;
  border-radius: 5px;
  flex-wrap: wrap;
  min-height: 13rem;
  min-width: 30rem;
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 1px 2px -2px rgba(0, 0, 0, 0.16),
    0 3px 6px 0 rgba(0, 0, 0, 0.12), 0 5px 12px 4px rgb(0 0 0 / 45%);
}

.popup-btn {
  display: flex;
  justify-content: space-between;
  margin-top: 3em;
}
.review-action-btn {
  display: flex;
  justify-content: space-between;
  width: 4em;
}

.danger-btn {
  background-color: #f04b4c;
  color: white;
  border: 1px solid #f04b4c;
  height: 3rem;
  width: 8rem;
  cursor: pointer;
  font-weight: 900;
}

.cancel-btn {
  background-color: white;
  color: #f04b4c;
  border: 1px solid #f04b4c;
  height: 3rem;
  width: 8rem;
  cursor: pointer;
  font-weight: 900;
}

.book-search-bar {
  width: 50%;
  margin: 2em auto;
}
.book-search-bar input {
  width: 100%;
  font-size: 1.3em;
  padding: 0.3em;
}

* {
  box-sizing: border-box;
}

.add-book-container {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

#contact input[type='text'],
#contact textarea,
#contact button[type='button'] {
  font: 400 16px/16px 'Roboto', Helvetica, Arial, sans-serif;
}

#contact {
  background: #f9f9f9;
  padding: 25px;
  margin: 90px 0;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

#contact h3 {
  display: block;
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 10px;
}

#contact h4 {
  margin: 5px 0 15px;
  display: block;
  font-size: 13px;
  font-weight: 400;
}

fieldset {
  border: medium none !important;
  margin: 0 0 10px;
  min-width: 100%;
  padding: 0;
  width: 100%;
}

#contact input[type='text'],
#contact textarea {
  width: 100%;
  border: 1px solid #ccc;
  background: #fff;
  margin: 0 0 5px;
  padding: 10px;
}

#contact textarea {
  height: 100px;
  max-width: 100%;
  resize: none;
}

#contact button[type='button'] {
  cursor: pointer;
  width: 100%;
  border: none;
  background: #0967c0;
  color: #fff;
  margin: 3px 0 5px;
  padding: 10px;
  font-size: 15px;
}

#contact button[type='button']:active {
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5);
}

#contact input:focus,
#contact textarea:focus {
  outline: 0;
  border: 1px solid #aaa;
}
